<template>
  <div>
    <Heads :title="languages.a12[langIndex]"></Heads>
    <main>
      <div class="lop">
        <van-button to="/lop" size="large" color="#dc0000">{{languages.a28[langIndex]}}</van-button>
      </div>
      <div class="cop">
        <van-button to="/cop" size="large" color="#dc0000">{{languages.a13[langIndex]}}</van-button>
      </div>
    </main>
  </div>
</template>

<style scoped>
main {
  width: 100%;
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.van-button--large {
  width: 4.8rem;
  height: 2rem;
  border-radius: 0.2rem;
  font-size: 0.6rem;
}
.lop {
  margin-bottom: 1rem;
}
</style>

<script>
import Heads from "@/components/Head";

export default {
  name: "Configure",
  components: { Heads },
  data() {
    return {};
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    }
  },
  mounted() {
    this.$store.commit("getTabledata", null); // 清空table数据
    this.$store.commit("getFloorbool", null); // 清空判断数据
    this.$store.commit("getFloormap", null); // 清空floorname
    this.$store.commit("getTablenum", null); // 清空表格数量
  }
};
</script>